import React from "react"
import { graphql, useStaticQuery, Link } from "gatsby"

import Layout from "../../components/_App/layout"
import Seo from "../../components/_App/seo"
import Navbar from "../../components/_App/Navbar"
import PageBanner from "../../components/Common/PageBanner"
import BlogCard from "../../components/BlogContent/BlogCard"
import Footer from "../../components/_App/Footer"
import playwright from "../../images/blog/playwright.png"
import BlogSidebar from "../../components/BlogContent/BlogSidebar"
import { useState, useEffect } from "react"

const BlogPage = () => {
  const data = useStaticQuery(graphql`
    query {
      allBloggerPost {
        edges {
          node {
            id
            title
            published
            content
            url
            slug
            images {
              url
            }
            author {
              displayName
              image {
                url
              }
            }
          }
        }
      }
    }
  `)
  // Categorize posts based on keywords in title/content
  const categoryKeywords = {
    "quality assurance": [
      "quality assurance",
      "qa",
      "Testing Framework",
      "testing",
      "playwright",
      "Delivering Quality, Phase 2",
    ],
    devops: [
      "devops",
      "Kubernetes",
      "Dev Containers",
      "pipeline",
      "Self-Contained Systems",
      "ELK in Kubernetes Production",
    ],
    "ai & ml": [
      "Unleashing AI Innovation",
      "machine learning",
      "artificial intelligence",
      "Highlights from Our Internal Hackathon on AI Agents & Automation",
    ],
    "ui/ux": ["From Concept to Clickable", "figma"],
    general: [],
  }

  function getCategory(title, content) {
    const lowerTitle = title.toLowerCase()
    const lowerContent = content ? content.toLowerCase() : ""
    for (const [cat, keywords] of Object.entries(categoryKeywords)) {
      if (
        keywords.some(k => lowerTitle.includes(k) || lowerContent.includes(k))
      ) {
        return cat
      }
    }
    return "General"
  }

  // Add category to each post
  const posts = data.allBloggerPost.edges.map(({ node }) => ({
    ...node,
    category: getCategory(node.title, node.content),
  }))

  // Get category from query param
  const [selectedCategory, setSelectedCategory] = useState(null)
  const [isMobile, setIsMobile] = useState(false)
  const searchParam =
    typeof window !== "undefined" ? window.location.search : ""
  useEffect(() => {
    if (typeof window !== "undefined") {
      const params = new URLSearchParams(window.location.search)
      const cat = params.get("category")
      setSelectedCategory(
        cat ? decodeURIComponent(cat.replace(/-/g, " ")) : null
      )
      setCurrentPage(1) // Reset pagination when filter changes
    }
  }, [searchParam])

  // Detect mobile view
  useEffect(() => {
    function handleResize() {
      setIsMobile(window.innerWidth < 992)
    }
    if (typeof window !== "undefined") {
      handleResize()
      window.addEventListener("resize", handleResize)
      return () => window.removeEventListener("resize", handleResize)
    }
  }, [])

  // Handle mobile dropdown category change
  function handleCategoryChange(slug) {
    if (typeof window !== "undefined") {
      const url = slug === "all" ? "/blog" : `/blog?category=${slug}`
      window.location.href = url
    }
  }

  // Pagination logic
  const POSTS_PER_PAGE = 8
  const FIRST_PAGE_POSTS = 7
  const [currentPage, setCurrentPage] = React.useState(1)
  // ...existing code...

  // Map sidebar slugs to keywords object keys
  const categorySlugToKey = {
    "quality-assurance": "quality assurance",
    devops: "devops",
    "ai-ml": "ai & ml",
    "ui-ux": "ui/ux",
    general: "general",
    all: "all",
  }

  // Helper to normalize strings for matching
  const normalize = str => str.toLowerCase().replace(/\s+/g, "")

  let filteredPosts = posts
  if (selectedCategory && selectedCategory !== "all") {
    if (selectedCategory === "general") {
      filteredPosts = posts.filter(post => post.category === "General")
    } else {
      filteredPosts = posts.filter(post => {
        const key =
          categorySlugToKey[
            selectedCategory.replace(/ /g, "-").toLowerCase()
          ] || selectedCategory.toLowerCase()
        const includesArr = categoryKeywords[key]
        if (!includesArr) return false
        const titleNorm = post.title ? normalize(post.title) : ""
        return includesArr.some(str => titleNorm.includes(normalize(str)))
      })
    }
  }

  // Adjust totalPages: first page shows 5, others show 6
  const totalPages =
    Math.ceil((filteredPosts.length - FIRST_PAGE_POSTS) / POSTS_PER_PAGE) + 1

  // Get posts for current page
  let paginatedPosts
  if (currentPage === 1) {
    paginatedPosts = filteredPosts.slice(0, FIRST_PAGE_POSTS)
  } else {
    const startIdx = FIRST_PAGE_POSTS + (currentPage - 2) * POSTS_PER_PAGE
    const endIdx = startIdx + POSTS_PER_PAGE
    paginatedPosts = filteredPosts.slice(startIdx, endIdx)
  }

  return (
    <Layout>
      <Navbar />
      <PageBanner
        pageTitle="Blogs"
        homePageText="Home"
        homePageUrl="/"
        activePageText="Blogs"
      />
      <div className="blog-area ptb-40">
        <div className="container">
          <div className="row">
            {/* BlogSidebar dropdown for mobile */}
            <div className="col-12 d-block d-lg-none mb-3">
              <BlogSidebar
                mobile={true}
                onCategoryChange={handleCategoryChange}
                selectedCategory={
                  selectedCategory
                    ? selectedCategory.replace(/ /g, "-").toLowerCase()
                    : "all"
                }
              />
            </div>
            {/* Blog cards and Playwright card */}
            <div className="col-lg-9 col-md-8">
              <div className="row">
                {(currentPage === 1 &&
                  (!selectedCategory || selectedCategory === "all")) ||
                (selectedCategory &&
                  (selectedCategory.toLowerCase() === "quality assurance" ||
                    selectedCategory.toLowerCase() === "quality-assurance")) ? (
                  <div className="col-lg-6 col-md-6 mt-40">
                    <div className="single-blog-post bg-fffbf5">
                      <div className="post-image">
                        <a
                          href="https://appnetwise.com/playwright-ui-automation/"
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <img
                            src={playwright}
                            alt="Playwright UI Automation"
                          />
                        </a>
                      </div>
                      <div className="post-content">
                        <ul className="post-meta d-flex justify-content-between align-items-center">
                          <li>
                            <i className="flaticon-user"></i> AppNetWise
                          </li>
                          <li>
                            <i className="flaticon-calendar"></i> 9/26/2025
                          </li>
                        </ul>
                        <h3>
                          <a
                            href="https://appnetwise.com/playwright-ui-automation/"
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            Playwright UI Automation
                          </a>
                        </h3>
                        <p className="mt-2 text-gray-700 text-sm">
                          Explore our modern, reliable UI automation solution
                          powered by Playwright.
                        </p>
                      </div>
                    </div>
                  </div>
                ) : null}
                {/* Paginated blog cards */}
                {paginatedPosts.map(post => (
                  <BlogCard key={post.id} post={post} />
                ))}
              </div>
              {/* Pagination UI: show only if more than 6 posts */}
              {filteredPosts.length > 6 && (
                <div className="col-lg-12 col-md-12">
                  <div className="pagination-area text-center">
                    <Link
                      to="#"
                      className="prev page-numbers"
                      onClick={e => {
                        e.preventDefault()
                        setCurrentPage(p => Math.max(1, p - 1))
                      }}
                      aria-disabled={currentPage === 1}
                      tabIndex={currentPage === 1 ? -1 : 0}
                      style={
                        currentPage === 1
                          ? { pointerEvents: "none", opacity: 0.5 }
                          : {}
                      }
                    >
                      <i className="bx bx-chevrons-left"></i>
                    </Link>
                    {[...Array(totalPages)].map((_, idx) => (
                      <Link
                        to="#"
                        key={idx + 1}
                        className={`page-numbers${
                          currentPage === idx + 1 ? " current" : ""
                        }`}
                        aria-current={
                          currentPage === idx + 1 ? "page" : undefined
                        }
                        onClick={e => {
                          e.preventDefault()
                          setCurrentPage(idx + 1)
                        }}
                      >
                        {idx + 1}
                      </Link>
                    ))}
                    <Link
                      to="#"
                      className="next page-numbers"
                      onClick={e => {
                        e.preventDefault()
                        setCurrentPage(p => Math.min(totalPages, p + 1))
                      }}
                      aria-disabled={currentPage === totalPages}
                      tabIndex={currentPage === totalPages ? -1 : 0}
                      style={
                        currentPage === totalPages
                          ? { pointerEvents: "none", opacity: 0.5 }
                          : {}
                      }
                    >
                      <i className="bx bx-chevrons-right"></i>
                    </Link>
                  </div>
                </div>
              )}
            </div>
            {/* Sidebar for desktop */}
            <div className="col-lg-3 col-md-4 mb-4 mt-40 d-none d-lg-block">
              <BlogSidebar
                selectedCategory={
                  selectedCategory
                    ? selectedCategory.replace(/ /g, "-").toLowerCase()
                    : "all"
                }
              />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </Layout>
  )
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => (
  <Seo
    title="Blog"
    description="Explore AppNetWise's blog for articles on AI innovation, software architecture, and digital transformation strategies, providing valuable insights to enhance your business."
  />
)

export default BlogPage
