import React from "react"
import { Link } from "gatsby"
import BlogSidebarDropdown from "./BlogSidebarDropdown"

const BlogSidebar = ({ mobile, onCategoryChange, selectedCategory }) => {
  // Categories should match those used in blog categorization
  const categories = [
    { name: "All", slug: "all" },
    { name: "Quality Assurance", slug: "quality-assurance" },
    { name: "DevOps", slug: "devops" },
    { name: "AI & ML", slug: "ai-ml" },
    { name: "UI/UX", slug: "ui-ux" },
    { name: "General", slug: "general" },
  ]

  // Get current category from query param (desktop fallback)
  let currentCategory = selectedCategory || "all"
  if (!selectedCategory && typeof window !== "undefined") {
    const params = new URLSearchParams(window.location.search)
    currentCategory = params.get("category") || "all"
  }

  // Mobile dropdown
  if (mobile) {
    return (
      <BlogSidebarDropdown
        categories={categories}
        currentCategory={currentCategory}
        onCategoryChange={onCategoryChange}
      />
    )
  }

  // Desktop sidebar
  return (
    <div className="services-details-info service-sidebar">
      <ul className="services-list">
        {categories.map(cat => {
          const isActive = currentCategory === cat.slug
          return (
            <li key={cat.slug}>
              <Link
                to={cat.slug === "all" ? "/blog" : `/blog?category=${cat.slug}`}
                className={isActive ? "active" : ""}
              >
                {cat.name}
              </Link>
            </li>
          )
        })}
      </ul>
      <div className="services-contact-info">
        <div className="text-center  ">
          <h4 className="mt-3">
            <strong>Schedule a Meeting with Us</strong>
          </h4>
          <p className="mb-3">Book a time slot that works best for you.</p>
          <div className="others-option d-flex align-items-center justify-content-center">
            <div className="option-item">
              <a
                href="https://outlook.office365.com/book/ConnectwithAppNetWise@appnetwise.com/"
                target="_blank"
                rel="noopener noreferrer"
                className="default-btn "
              >
                <i className="flaticon-calendar mr-4"></i> Book a Demo
                <span></span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default BlogSidebar
