import React from "react"
import Select from "react-select"

const BlogSidebarDropdown = ({
  categories,
  currentCategory,
  onCategoryChange,
}) => {
  const options = categories.map(cat => ({ value: cat.slug, label: cat.name }))
  const selectedOption =
    options.find(opt => opt.value === currentCategory) || options[0]

  return (
    <div className="blog-mobile-dropdown mb-3">
      <label
        htmlFor="blog-category-select"
        style={{
          fontWeight: 600,
          marginBottom: 8,
          display: "block",
          fontSize: 16,
        }}
      >
        <i
          className="flaticon-menu"
          style={{ marginRight: 8, color: "#0C7264" }}
        ></i>
        Filter by Category
      </label>
      <Select
        inputId="blog-category-select"
        options={options}
        value={selectedOption}
        onChange={option => onCategoryChange(option.value)}
        styles={{
          container: base => ({ ...base, width: "100%" }),
          control: base => ({
            ...base,
            borderRadius: 8,
            minHeight: 44,
            fontSize: 15,
          }),
          menu: base => ({ ...base, zIndex: 9999 }),
        }}
        theme={theme => ({
          ...theme,
          borderRadius: 8,
          colors: {
            ...theme.colors,
            primary25: "#e6f0fa",
            primary: "#0C7264",
          },
        })}
        isSearchable={false}
      />
    </div>
  )
}

export default BlogSidebarDropdown
